<template>
  <div>
    <div class="md-container">
      <LevelComponent 
        :points="5" 
        :badPointIndexes="badPoint"
        v-model="isLoser"
        @pointClick="(e) => handlePointClick(e)"
        v-for="(badPoint, idx) in badPoints" 
        :key="idx"
        :disabled="currentLevel != (badPoints.length - idx) % 6 || !inGame"
        :level="(badPoints.length - idx) % 6"
        :inGame="inGame"
      ></LevelComponent>
    </div>
    <div v-if="inGame" style="display: flex; align-items: center">
      Ставка {{ deposit ? deposit.toFixed(2) : null }}
      Выигрыш: {{ maybeWin.toFixed(2) }}
      <md-button @click="endGame()" :disabled="maybeWin == 0" class="md-accent">
        Забрать выигрыш
      </md-button>
    </div>
    <div v-else-if="!inGame && isLoser">
      Проёбка
    </div>
    <div style="display: flex; align-items: center;">
      <md-field>
        <label>Депозит</label>
        <md-input v-model="inputDeposit" type="number"></md-input>
      </md-field>
      <md-button :disabled="inGame || inputDeposit < 10" @click="startGame(inputDeposit)" class="md-accent">
        Депаем
      </md-button>
    </div>
    <div>
      Баланс: {{ balance.toFixed(2) }} ₽
    </div>
  </div>
</template>

<script>
import LevelComponent from "./Level.vue"
export default {
  name: 'HelloWorld',
  components: {LevelComponent},
  data() {
    return {
      levels: 5,
      isLoser: false,
      badPoints: [],
      currentLevel: 1,
      inGame: false,
      balance: 5000,
      deposit: null,
      maybeWin: 0,
      inputDeposit: null
    }
  },
  mounted(){
    this.getBadPoints();
  },
  methods: {
    generateBadPointIndex(min, max){
      return Math.floor(Math.random() * (max - min + 1) + min)
    },
    handlePointClick(pointClickResult){
      if (!pointClickResult){
        this.maybeWin = this.deposit * (0.3 * this.currentLevel + 1)
        this.currentLevel += 1;
      } else if (this.currentLevel == 5) {
        this.endGame();
      } else {
        this.maybeWin = 0;
        this.inGame = false;
        this.isLoser = pointClickResult;
        this.currentLevel = 1
      }
    },
    endGame(){
      this.inGame = false;
      this.balance += this.maybeWin;
      this.maybeWin = 0;
      this.currentLevel = 1;
    },
    getBadPoints(){
      this.badPoints = []
      for (let i = 1; i < this.levels + 1; i++){
        const badPointsRow = []
        while(badPointsRow.length < i && badPointsRow.length < this.levels - 2){
            var r = this.generateBadPointIndex(1, 5);
            console.log(r)
            if(badPointsRow.indexOf(r) === -1) badPointsRow.push(r);
        }
        this.badPoints.push(badPointsRow)
      }
      this.badPoints = this.badPoints.reverse()
      console.log(this.badPoints)
    },
    startGame(deposit) {
      this.deposit = Number(deposit);
      this.inputDeposit = null;
      this.balance -= deposit;
      this.inGame = true;
      this.getBadPoints();
      this.currentLevel = 1;
      this.isLoser = false;
      console.log(this.deposit)
    }
  },
  watch: {
    inputDeposit(newValue){
      console.log(newValue > this.balance)
      this.inputDeposit = newValue > this.balance ? this.balance : newValue;
    }
  }
}
</script>

