<template>
    <div style="display: flex; align-items: center">
        <div class="md-layout">
            <Point
                :bgColor="value ? isBadPoint(point) ? 'red' : 'green' : checkedPoint == point ? 'green' : 'gray'"
                @click="() => handlePointClick(point)"
                class="md-layout-item" 
                v-for="point in points" 
                :key="point"
                :point=point
                :isLoser="isLoser"
                :disabled="checkedPoint != null || value || disabled"
            >
            </Point>
        </div>
        <div style="width: 15px; margin-left: 5px">
            x{{ 0.3 * level + 1 }}
        </div>
    </div>
</template>

<script>
import Point from "./Point.vue"
export default {
    components: {Point},
    props: [
        "points", 
        "badPointIndexes", 
        "isLoser", 
        "value", 
        "disabled",
        "level",
        "inGame"
    ],
    data() {
        return {
            checkedPoint: null
        }
    },
    methods: {
        isBadPoint(point){
            console.log(point, this.badPointIndexes)
            return this.badPointIndexes.includes(point);
        },
        handlePointClick(point){
            const pointClickResult = this.isBadPoint(point);
            this.$emit("pointClick", pointClickResult);
            console.log(pointClickResult)
            if (!pointClickResult){
                this.checkedPoint = point
            }
        },
    },
    computed: {
        bgColor() {
            if (this.value){
                return "red"
            } else if (this.checked) {
                return "green"
            } else {
                return "#456"
            }
        }
    },
    watch: {
        inGame(){
            this.checkedPoint = null;
        }
    }
}
</script>

<style>
.md-layout-item {
    height: 40px;
    background-color: grey;
    color: white;
    align-items: center;
}
.md-layout {
    align-items: center;
}
</style>